import React, { Component }  from 'react';
import applause from './imgs/applause.mp3';
import {Animated} from "react-animated-css";
import image311 from './imgs/img3/img3.1.1.jpg';
import image312 from './imgs/img3/img3.1.2.jpg';
import image313 from './imgs/img3/img3.1.3.jpg';
import image321 from './imgs/img3/img3.2.1.jpg';
import image322 from './imgs/img3/img3.2.2.jpg';
import image323 from './imgs/img3/img3.2.3.jpg';
import image331 from './imgs/img3/img3.3.1.jpg';
import image332 from './imgs/img3/img3.3.2.jpg';
import image333 from './imgs/img3/img3.3.3.jpg';
import image341 from './imgs/img3/img3.4.1.jpg';
import image342 from './imgs/img3/img3.4.2.jpg';
import image343 from './imgs/img3/img3.4.3.jpg';


class Puzzle3 extends Component{
    constructor(props){
        super(props);
        this.state = { 
            completion: false,
            dragStartId: '',
        };
        this.dragStart = this.dragStart.bind(this);
        this.dragOver = this.dragOver.bind(this);
        this.dragDrop = this.dragDrop.bind(this);
    }

    
    componentDidMount() {

        //CHECK PUZZLE COMPLETION
        let items = document.querySelectorAll("img");
        let ifComplete = this.state.completion;
        var sound = new Audio(applause);

        const myGameCheck = () => {
        setInterval(() => {
            var flag = 0;
            const imageList = [
                "data:image/jpeg;base64,",
                "data:image/jpeg;base64,",
                "data:image/jpeg;base64,",
                "data:image/jpeg;base64,",
                "data:image/jpeg;base64,",
                "data:image/jpeg;base64,",
                "data:image/jpeg;base64,",
                "data:image/jpeg;base64,",
                "data:image/jpeg;base64,",
                "data:image/jpeg;base64,",
                "data:image/jpeg;base64,",
                "data:image/jpeg;base64,",
            ];
            for (var i=0; i<imageList.length; i++) {
                if(items[i].src === imageList[i]) {
                    ++flag;
                }
                if(this.state.completion === false) {
                    console.log("ITEM "+[i+1]+" "+items[i].src);
                }
            }
            if(flag === 12){
                for (var j=0; j<imageList.length; j++) {
                    document.querySelectorAll('.list img')[j].style.margin = "0px";
                }
                document.querySelector('.list').style.boxShadow = "5px 5px 15px black, -5px -5px 15px lightgray";
                document.querySelector('.list').style.transform = "scale(1.1)";
                document.querySelector('.list').style.transition = "0.8s";
                if(this.state.completion === false){
                    sound.volume = 0.05;
                    sound.play(); 
                }                            
                this.setState({completion: true});
                ifComplete = this.state.completion;
            }
        }, 1000);}
        myGameCheck();


        // START COUNTDOWN-TIMER
        var countdown = 55;
        const myInterval = setInterval(myTimer, 1000);
        function myTimer() {
            countdown = countdown-1;
            document.getElementById('clock').innerHTML = countdown;
            if( ifComplete === true){
                document.getElementById('clock').innerHTML = "00";
                document.getElementById('clock').style.backgroundColor = "white";
                clearInterval(myInterval);
            }
            if(countdown < 1) {
                document.getElementById('clock').innerHTML = "00";
                document.getElementById('clock').style.backgroundColor = "red";
                clearInterval(myInterval);
                setTimeout(function(){
                    window.location.replace("web_Game3-Jigsaw/");
                }, 1000);
            }
        }
    };

    
    // Drag & Drop Listeners
    dragStart(e) {
        // console.log("Start", e.target.id);
        var varStart = e.target.id;
        this.setState({dragStartId: varStart});
    }
    dragOver(e) {
        // console.log('Event: ', 'dragOver');
        e.preventDefault();
    }
    dragDrop(e) {
        e.preventDefault();
        // console.log("End", e.target.id);
        var varEnd = e.target.id;
        this.setState({dragEndIndex: varEnd});
        this.swapItems(this.state.dragStartId, varEnd);
    }
    // Swap List Items
    swapItems(firstId, SecondId) {
        const firstImage = document.getElementById(firstId).src;
        const secondImage = document.getElementById( SecondId).src;
        // console.log(firstClass, secondClass);
        document.getElementById(SecondId).src = firstImage;
        document.getElementById(firstId).src = secondImage;  
    }


    render(){
    return (
        <div className="Puzzle3">      
            <h1>Puzzle</h1>
            <Animated animationIn="bounceInDown" animationOut="fadeOut" isVisible={true}>
            <p id="mobileCompatibility">Incompatible with mobile-devices</p>
            <h2 id="clock">55</h2>
            </Animated>

            <div className="box">
                <div className="list" >
                    <img src={image311} id="one" draggable="true" alt="puzzle-img" onDragStart={this.dragStart} onDragOver={this.dragOver} onDrop={this.dragDrop}></img>
                    <img src={image312} id="two" draggable="true" alt="puzzle-img" onDragStart={this.dragStart} onDragOver={this.dragOver} onDrop={this.dragDrop}></img>
                    <img src={image313} id="three" draggable="true" alt="puzzle-img" onDragStart={this.dragStart} onDragOver={this.dragOver} onDrop={this.dragDrop}></img>
                    <img src={image321} id="four"  draggable="true" alt="puzzle-img" onDragStart={this.dragStart} onDragOver={this.dragOver} onDrop={this.dragDrop}></img>
                    <img src={image322} id="five" draggable="true" alt="puzzle-img" onDragStart={this.dragStart} onDragOver={this.dragOver} onDrop={this.dragDrop}></img>
                    <img src={image323} id="six" draggable="true" alt="puzzle-img" onDragStart={this.dragStart} onDragOver={this.dragOver} onDrop={this.dragDrop}></img>
                    <img src={image331} id="seven" draggable="true" alt="puzzle-img" onDragStart={this.dragStart} onDragOver={this.dragOver} onDrop={this.dragDrop}></img>
                    <img src={image332} id="eight" draggable="true" alt="puzzle-img" onDragStart={this.dragStart} onDragOver={this.dragOver} onDrop={this.dragDrop}></img>
                    <img src={image333} id="nine" draggable="true" alt="puzzle-img" onDragStart={this.dragStart} onDragOver={this.dragOver} onDrop={this.dragDrop}></img>
                    <img src={image341} id="ten" draggable="true" alt="puzzle-img" onDragStart={this.dragStart} onDragOver={this.dragOver} onDrop={this.dragDrop}></img>
                    <img src={image342} id="eleven" draggable="true" alt="puzzle-img" onDragStart={this.dragStart} onDragOver={this.dragOver} onDrop={this.dragDrop}></img>
                    <img src={image343} id="twelve" draggable="true" alt="puzzle-img" onDragStart={this.dragStart} onDragOver={this.dragOver} onDrop={this.dragDrop}></img>
                </div>
            </div>

            <div className="goBack"><a href="https://immanuel404.github.io/web_Game3-Jigsaw/">Back</a></div>
            <div className="footer2">
                <p>&copy;<a href="http://webcitizen.epizy.com/">webcitizen(2022)</a></p>
            </div>
        </div>
    ); }
  }
  export default Puzzle3;